<template>
  <div class="index">

    <div class="index-content">

      <!-- 二级路由 -->
      <router-view></router-view>

    </div>
    
    <!-- 底部导航 -->
    <div class="tabs">
      <div v-for="(item, index) in tabsData" :key="index" :class="{active: item.isActive}" @click="changeState(item)">{{item.title}}</div>
    </div>

  </div>
</template>

<!-- scoped: 该样式只会在当前组件才有效 -->
<style scoped lang="less">
  .index{

    .tabs{
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      border-top: 1px solid #ddd;
      >div{
        float: left;
        width: 20%;
        height: 59px;
        line-height: 59px;
        text-align: center;
        color: #444;
        background-color: #fff;
      }

      >div.active{
        background-color: #e4393c;
        color: #fff;
      }
    }

    

  }
</style>

<script>
  export default {
    name: 'index',

    data() {
      return {

        tabsData: [
          {
            name: 'x',
            title: '首页',
            isActive: true,
             routerUrl: ['/x', '/y']
          },
          {
            name: 'a',
            title: '分类',
            isActive: false,
             routerUrl: ['/a', '/b']
          },
          {
            name: 'jingxi',
            title: '京喜',
            isActive: false,
            routerUrl: ['/jingxi']
          },
          {
            name: 'shopcart',
            title: '购物车',
            isActive: false,
             routerUrl: ['/shopcart']
          },
          {
            name: 'mine',
            title: '我的',
            isActive: false,
             routerUrl: ['/mine']
          }
        ]

      };
    },

    methods: {

      changeState(item) {
        //如果当前已经是激活状态的，则拦截执行
        if (item.isActive) {
          return;
        }

        //查找其他激活的tab
        for (let i = 0; i < this.tabsData.length; i++) {
          //查找其他激活的tab，找到修改为未激活状态，同时把当前改为激活状态
          if (this.tabsData[i].isActive) {
            this.tabsData[i].isActive = false;
            item.isActive = true;
            break;
          }

        }

        //切换二级路由
        this.$router.push({name: item.name})

      }

    },

    created() {

      //纠正刷新的时候，激活的tab不一致
        let p = location.hash.split('?')[0].slice(1);
      // console.log(path);

      for (let i = 0; i < this.tabsData.length; i++) {
        this.tabsData[i].isActive = this.tabsData[i].routerUrl.indexOf(p) > -1;
      }
      
    }
  }
</script>
